@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&family=Nanum+Gothic&display=swap);
@font-face {
    font-family: 'GothamMedium';
    src: local("GothamMedium"), url(/react-tesla-clone/static/media/GothamMedium.0a910c0b.ttf) format('truetype');
    font-weight: normal;
}

@font-face {
    font-family: 'GothamBook';
    src: local("GothamBook"), url(/react-tesla-clone/static/media/GothamBook.58fb9151.ttf) format('truetype');
    font-weight: normal;
}

* {
    box-sizing: border-box;
    font-family: GothamMedium, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    color: #393c41;
    margin: 0;
    padding: 0;
    border: none;
}

body {
    width: 100%;
    margin: 0;
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@-webkit-keyframes animateDown {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    40% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }

    60% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}

@keyframes animateDown {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    40% {
        -webkit-transform: translateY(5px);
                transform: translateY(5px);
    }

    60% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
    }
}

@media (min-width: 1024px) {
    *::-webkit-scrollbar {
        width: 10px;
    }

    *::-webkit-scrollbar-track {
        background-color: transparent;
    }

    *::-webkit-scrollbar-thumb {
        background-color:#bdbdbc;
        border-radius: 18px;
        border: 2px solid #fff;
        height: 224px;
    }

    *::-webkit-scrollbar-thumb:hover {
        background-color:#727473;
    }
}
  
.link {
    display: inline;
    color: #5c5d61;
    font-size: 14px;
    font-family: GothamBook, sans-serif;
    padding: 0 0 2px;
    cursor: pointer;
    font-weight: bold;
    text-decoration: none;
    transition: 0.4s ease-out 100ms;
    box-shadow: 0 1px 0 0 #5c5d61;
    letter-spacing: 0.4px;
}

.link:hover {
    color: #181b21;
    box-shadow: 0 2px 0 0 #5c5d61;
}



.drawer {
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
}

.drawer .box .list-item {
    border-radius: 12px;
    padding: 2px 12px;
    margin: 4px 0;
}

.drawer .box .list-item span {
    font-size: 14px;
    font-family: GothamBook, sans-serif;
    font-weight: 600;
}

#lang-item-text-country span {
    font-size: 14px;
    font-family: GothamBook, sans-serif;
    font-weight: 600;
}

#lang-item-text-lang span {
    font-size: 12px;
    font-family: GothamBook, sans-serif;
}

